@font-face {
  font-family: 'mononoki-BoldItalic';
  src: url('../../utils/fonts/mononoki-BoldItalic.ttf') format('truetype');
}

.darkMode,
.lightMode {
  width: 250px;
  height: 100%;
  position: absolute;
  top: 0px;
  z-index: 2000;
  transition: all 0.5s;
  overflow: hidden;

  .select {
    position: relative;
    display: inline-block;
    border: 1px solid white;
    border-radius: 10px;
    padding: 5px;
    color: white;
    font-weight: bold;
    appearance: none;
    cursor: pointer;
    transition: all 0.5s;
    width: 200px;
    text-align: center;
  }

  .select:focus,
  .select:hover {
    /* border-color: #ffffff; */
    border: 3px solid #eeeeee;
  }

  .select:active {
    border-color: rgb(31, 199, 183);
  }

  .modeButton {
    left: 70%;
    top: 40px;
    padding: 5px;
    border-radius: 50%;
    transition: all 0.5s;
  }

  .content {
    border: 1px solid transparent;
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    color: white;
    padding: 0 10px;
    border: 1px solid transparent;
  }
}

.titleContent {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin: 0;
  padding: 0 40px 0 0;
  font-family: "mononoki-BoldItalic";
}

.logo {
  margin: 0 5px 0 0;
  color: white;
}

.list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
}

.itemList {
  margin: 15px 0;
  display: flex;
  align-items: center;
}

a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.navOptions {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 20px 10px;
}

.hamburguer {
  /* position: absolute; */
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.darkMode {
  background: linear-gradient(to bottom, #335855, #058a7f);
  .select {
    background: linear-gradient(to bottom, #335855, #058a7f);
  }
  .modeButton {
    background: transparent;
    cursor: pointer;
  }

  .select:focus,
  .select:hover {
    /* border-color: #ffffff; */
    border: 3px solid #197794;
    color:#fff;
    background: #335855;
  }

  .button {
    background: linear-gradient(to bottom, #335855, #058a7f);
    &:hover{
      background: #335855;
    }
  }
}

.lightMode {
  background: linear-gradient(to bottom, rgb(155, 118, 80), rgb(182, 149, 116)) ;
  .select {
    background: linear-gradient(to bottom, rgb(155, 118, 80), rgb(182, 149, 116));
  }
  .modeButton {
    background: transparent;
    cursor: pointer;
  }
  .select:focus,
  .select:hover {
    /* border-color: #ffffff; */
    border: 3px solid #ffffff;
    color:#ffffff;
    background: rgb(182, 149, 116);
  }

  .button {
    background: linear-gradient(to bottom, rgb(155, 118, 80), rgb(182, 149, 116));
    transition: all .5s;
    &:hover{
      background: rgb(189, 160, 131);
    }
  }
}
/*  */
.open {
  animation: typeOpen .75s normal;
  height: 100%;
  border-radius: 0 50px 0 0;
}

.close {
  height: 40px;
  animation: typeClose .75s normal;
  background: transparent;
}

@keyframes typeOpen {
  0% {
    height: 40px;
    background: transparent;
  }
  100% {
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
  }
}
@keyframes typeClose {
  0% {
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
  }
  100% {
    height: 40px;
    background: transparent;
  }
}


.button {
  padding: 7px 0;
  border: 1px solid white;
  border-radius: 10px;
  width: 200px;
  text-align: center;
  cursor: pointer;

}
